import { FC, HTMLProps, Ref, useCallback, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './Checkbox.scss';

// Types
import { CheckOption, FormOptionOnChange, TestMetadata } from '../types';

export interface CheckboxProps {
  /** Custom class name(s) for component container */
  className?: string;
  /** Method to control changing the checkbox */
  onChange?: FormOptionOnChange;
  /** Check box options such as name and id */
  option: CheckOption;
  /** Disables the checkbox */
  disabled?: boolean;
  /** Value to set the checked state */
  checked?: boolean;
  /** Initial checked value **/
  defaultChecked?: boolean;
  /** Input reference **/
  inputRef?: Ref<HTMLInputElement>;
  /** Additional input props for Input, based on the inputComponent */
  inputProps?: HTMLProps<HTMLInputElement> & TestMetadata;
  /** Error State */
  isError?: boolean;
  // Test IDs for sections of Checkbox
  testMetadata?: TestMetadata;
}

const Checkbox: FC<CheckboxProps> = ({
  className,
  option,
  onChange,
  disabled,
  checked,
  defaultChecked,
  inputProps,
  inputRef,
  isError = false,
  testMetadata,
  ...otherProps
}) => {
  const inputClassNames = useMemo(
    () =>
      classNames(
        'sb-checkbox__input',
        {
          'sb-checkbox_error': isError,
        },
        inputProps?.className,
      ),
    [inputProps, isError],
  );
  const checkboxClassNames = useMemo(
    () =>
      classNames('sb-checkbox', className, {
        'sb-checkbox_disabled': disabled,
      }),
    [className, disabled],
  );

  const memoizedOnChange = useCallback(
    () => onChange?.(option.value),
    [onChange, option.value],
  );

  return (
    <div {...otherProps} className={checkboxClassNames}>
      <label htmlFor={String(option.id)} className="sb-checkbox__label">
        <input
          type="checkbox"
          name={String(option.name || option.id)}
          id={String(option.id)}
          disabled={disabled}
          checked={checked}
          onChange={memoizedOnChange}
          defaultChecked={defaultChecked}
          role="checkbox"
          ref={inputRef}
          {...testMetadata}
          {...inputProps}
          className={inputClassNames}
        />
        <div>
          <div className="sb-checkbox__label-text">{option.label}</div>
          {option.helpText && (
            <div className="sb-checkbox__helper">{option.helpText}</div>
          )}
        </div>
      </label>
    </div>
  );
};

export default Checkbox;
